#basic-navbar-nav {
    .nav-pills {
        .nav-item {
            .nav-link {
                color: inherit;
            }
            .nav-link.active a{
                color: #fff;
            }
        }
    }
}
// TODO: Fix if nav link;

.nav_Line1{
    display:flex;
    justify-content: space-between;
    padding-top:-0.5em;
    select {
        color: #000;
    }
}

.nav_Line2{
    display:flex;
    justify-content: space-between;
    border-top:1px solid #ddd;
}

.adressfont{
    font-family: 'Muli', sans-serif;
    font-size: 0.75em;
    color: #888;
   
}

.Logo{
    display:flex;
    justify-content: flex-start;
    align-items: center;
    background: #1f2021;
    height:7.0em;
    width:45em;
    text-decoration: none;
    h5{
        display: flex;
        flex-direction: column;
        color:white;
        margin-left: 0.4em;
        
    }
    
    a{
        font-size: 1.6em;

    }
    span{
        font-size:0.7em;
        font-family: 'Muli', sans-serif;
        letter-spacing: 3px;
        border-top: 1px solid #ddd;
    }
    
}

$navBlue:#0099e5;

.NavADD{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  
    .navAddress{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font: 1.2em sans-serif;
        margin-right:2.5em;

        #navAddressLogo{
            color:$navBlue;
            font-size: 1.2em;
            margin-right:1.0em;
        }

        #navAddress0 {
            color: #666;
        }
    
        #navAddressContent{
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            #navAddress0{
                display: flex;
                align-self:row;
                font-weight:bold;
              
            }
            #navAddress1{
                display: flex;
                align-self:row;
               
            }
        }
    }
    
    
    .navContact{
        display: flex;
        justify-content: flex-start;
        font: 1.2em sans-serif;
        align-items: center;
        #navContactLogo{
            color:$navBlue;
            font-size: 1.2em;
            margin-right:1.0em;
        }

        #navContact0 {
            color: #666;
        }
    
        #navContactContent{
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            #navContact0{
                display: flex;
                align-self:row;
                font-weight:bold;
            }
            #navContact1{
                display: flex;
                align-self:row;
               
            }
        }
    }

    .nav-item{

        a{
            color: inherit;
         
        }
    }
    

}




